
import { Options, Vue } from "vue-class-component";

@Options({
  components: {},
})
export default class Home extends Vue {
  get q1(): number {
    let a = [3, 2, 9, 1, 2, 3, 4];
    let b = (ac: number, cu: number) => ac + cu;
    let c = a.reduce(b);
    return c;
  }

  get q2(): number {
    let a = [{ x: 1 }, { x: 2 }, { x: 3 }];
    let b = (ac: number, cu: number) => ac + cu;
    let c = a.map((item) => item.x).reduce(b);
    return c;
  }

  get q3(): number[] {
    let a = [
      [0, 1],
      [2, 3],
      [4, 5],
    ];
    let b: any = (ac: [], cu: []) => ac.concat(cu);
    let c = a.reduce(b);
    return c;
  }

  get q4(): Record<string, number> {
    let a = ["Alice", "Bob", "Tiff", "Bruce", "Alice"];
    let b = (ac: Record<string, number>, cu: string) => {
      if (cu in ac) {
        ac[cu]++;
      } else {
        ac[cu] = 1;
      }
      return ac;
    };
    let c = a.reduce(b, {});
    return c;
  }

  get q5(): any {
    let people: {
      name: string;
      age: number;
      sex?: number;
    }[] = [
      { name: "Alice", age: 21, sex: 0 },
      { name: "Max", age: 20, sex: 1 },
      { name: "Jane", age: 20, sex: 0 },
    ];
    function groupBy(objAry: any[], property: string): any {
      return objAry.reduce((ac, cu) => {
        let key = cu[property];
        if (!ac[key]) {
          ac[key] = [];
        }
        ac[key].push(cu);
        return ac;
      }, {});
    }
    let result = groupBy(people, "age");
    return result;
  }

  get q6(): string[] {
    let friends = [
      {
        name: "Anna",
        books: ["Bible", "Harry Potter"],
        age: 21,
      },
      {
        name: "Bob",
        books: ["War and peace", "Romeo and Juliet"],
        age: 26,
      },
      {
        name: "Alice",
        books: ["The Lord of the Rings", "The Shining"],
        age: 18,
      },
    ];
    let a = (ac: string[], cu: any) => [...ac, ...cu.books];
    let b: string[] = friends.reduce(a, []);
    return b;
  }

  get q7(): number {
    return 1;
  }

  get q8(): number {
    return 2;
  }
  get q9(): number {
    return 3;
  }
}
